.ContactItem {
  width: 100%;
  &:not(:last-child) {
    scroll-margin-bottom: 1rem;
  }

  .contact {
    display: flex;
    width: 100%;
    height: 10rem;
    align-items: center;
    padding: 3rem 0;
    background-color: #191d2b;

    .right-items {
      margin-left: 2rem;
      // font-weight: 500;
      font-size: 1.2rem;
      // width: 16%;
    }
    img {
      padding: 1rem;
      height: 100%;
      border: 1px solid #2e344e;
      border-radius: 10rem;
      margin-left: 2rem;
    }
  }
}

.ContactPage {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 10rem;
  @media screen and (max-width: 1270px) {
    grid-template-columns: repeat(1, 1fr);
    .map-sect {
      height: 100%;
      height: 50vh;
      margin-bottom: 2rem;
    }
  }
  .contact-sect {
    width: 100%;
  }
}
.map-sect {
  height: 100%;
  padding: 1rem;
  background-color: #191d2b;
  iframe {
    height: 100%;
    width: 100%;
  }
}
.contact-sect {
  // margin-left: 2rem;
  border-left: 1px solid #2e344e;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.title {
  margin-bottom: 5rem;
}
