.App {
  .sidebar {
    width: 16%;
    height: 100vh;
    background-color: #10121b;

    // background-image: url("../img/dots.jpg");

    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(5px);
    position: fixed;
    z-index: 11;
    border-right: 1px solid #2e344e;
    transform-origin: left;
    transition: all 0.4s ease-in-out;
    @media screen and (max-width: 1000px) {
      background-image: none;
      transition: all 0.4s ease-in-out;
      transform: translateX(-100%);
      width: 30%;
    }
    @media screen and (max-width: 411px) {
      width: 50%;
    }
  }

  .nav-toggle {
    @media screen and (max-width: 1000px) {
      background: none;
      transition: all 0.4s ease-in-out;
      transform: translateX(0);
    }
  }

  .main-content {
    width: 84%;
    margin-left: 16%;
    background-color: #10121b;
    min-height: 100vh;
    // background-image: url("../img/dots.jpg");
    display: grid;
    position: relative;
    .content {
      margin: 5rem 12rem;

      @media screen and (max-width: 1400px) {
        margin: 3rem 10rem;
      }
      @media screen and (max-width: 1100px) {
        margin: 2rem 10rem;
      }
      @media screen and (max-width: 700px) {
        margin: 2rem 2rem;
      }
    }
    @media screen and (max-width: 1000px) {
      margin-left: 0;
      width: 100%;
    }
  }
}

.nav-btn {
  position: absolute;
  z-index: 10;
  right: 10%;
  top: 5%;
  width: 4rem;
  height: 4rem;
  display: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  .line-1,
  .line-2,
  .line-3 {
    height: 0.4rem;
    width: 100%;
    background-color: #3858cc;
    pointer-events: none;
    display: none;
    border-radius: 20px;
    &:not(:last-child) {
      margin-bottom: 0.4rem;
    }
  }
  @media screen and (max-width: 1000px) {
    display: block;
    .line-1,
    .line-2,
    .line-3 {
      display: block;
    }
  }
}
